import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWheel} from "@fortawesome/free-solid-svg-icons";
import tier from "../images/wheel-svgrepo-com.svg"
import $ from "jquery";
import { useState, useRef, useEffect } from "react";
import "./Section_1.css"
import img1 from "../images/2.png"
const Section_1 = ()=>{
    
    const [offset, setOffset] = useState(0);
    useEffect(() => {
        const onScroll = () => setOffset(window.pageYOffset);
        window.removeEventListener('scroll', onScroll);
        window.addEventListener('scroll', onScroll, { passive: true });
        return () => window.removeEventListener('scroll', onScroll);
    }, []);   

    window.addEventListener('scroll', ()=>{
        if(offset > 150) {  
            $(".img").css({left: "0"});
        
        }
        if(offset > 300) {  
            $(".section1Container").css({right: "0"});
            
        }
        if(offset < 70) {  
            $(".img").css({left: "100%"});
            $(".section1Container").css({right: "-100%"});
        }
        console.log(offset);
    })
    return (
        <section className="section1">
            <img className="img" src={img1}  alt="" />
            <div  className="section1Container">
                <h3>The all-new 2019 Audi A8</h3>
                <h2>Luxury found the vehicle it deserves.</h2>
                <p>Starting at $83,800</p>
            </div>
            {/*<FontAwesomeIcon icon={faCircle} fontSize={"200px"}/>*/}
            </section>
        )
    }
export default Section_1;
    
        
        
        
